import React from 'react'
import { Dropdown } from 'react-bootstrap'

const header = () => {
  return (
    <header className='header'>
      <div className='header__container'>
        <a href='#slider'>
          <div className='header__logo-container'>
            <img className='header__logo' src={process.env.PUBLIC_URL + '/logo-admiral.png'} alt=''></img>
            <div className='header__logo-text-container'>
              <h4 className='header__logo-text'>Admiral Fashion</h4>
            </div>
          </div>
        </a>
        <ul className='header__nav'>
          <li className='header__nav-text'><a href='#slider'></a></li>
          <li className='header__nav-text'><a href='#services'></a></li>
          <li className='header__nav-text'><a href='#catalog'></a></li>
          <li className='header__nav-text'><a href='#portfolio'> </a></li>
          <li className='header__nav-text'><a href='#contacts'></a></li>
        </ul>
        <Dropdown className="header__dropdown-container">
          <Dropdown.Toggle variant="success" id="dropdown-basic" className="header__dropdown">
            <img className='header__menu-mobile' src={process.env.PUBLIC_URL + '/menu.png'} alt=''></img>
          </Dropdown.Toggle>

          <Dropdown.Menu>
            <Dropdown.Item href="#slider"></Dropdown.Item>
            <Dropdown.Item href="#services"></Dropdown.Item>
            <Dropdown.Item href="#catalog"></Dropdown.Item>
            <Dropdown.Item href="#portfolio"> </Dropdown.Item>
            <Dropdown.Item href="#contacts"></Dropdown.Item>
          </Dropdown.Menu>
        </Dropdown>
      </div>
    </header>
  )
}

export default header